.header{
  padding: 1.875em 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  background-color: $white;
  border-radius: 0 0 .625em .625em;
  &--transparent{
    @media screen and (min-width: 993px){
      background-color: transparent;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 13.5em;
    margin-right: 3.125em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.mob-menu{
  .phone-link{
    display: none;
  }
}

.nav{
  .menu{
    display: flex;
    align-items: center;
    a{
      padding: 0 1em;
      display: flex;
      align-items: center;
      height: 2.4em;
      font-size: 1.25em;
      font-weight: 700;
    }
  }
}

._flex{
  display: flex;
  align-items: center;
}

.header-contacts{
  margin-left: auto;
  .phone-link{
    margin-left: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    border-radius: 0 0 1.25em 1.25em;
    &__inner{
      justify-content: flex-end;
    }
    &__logo{
      margin-right: auto;
      width: 9.6875em;
      position: relative;
      z-index: 11;
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-contacts{
    margin: 0;
    position: relative;
    z-index: 11;
    .phone-link{
      display: none;
    }

    .socials{
      &__item{
        width: 2.5em;
      }
    }
  }

  .mob-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    padding: 5em 0 0;
    background-color: $white;
    z-index: 10;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__inner{
      padding: 1em 1.25em 2em;
      height: 100%;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .phone-link{
      margin-top: auto;
      display: flex;
    }
  }

  .nav{
    .menu{
      flex-direction: column;
    }
  }

  .burger{
    position: relative;
    z-index: 11;
    @include center;
    width: 2.5em;
    height: 2.5em;
    border-radius: .625em;
    background-color: $black;
    margin-left: .625em;
    img{
      @include centerAbsolute;
      width: 1.5em;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
      width: 1em;
    }
  }
}


