.sections-group{
  overflow: hidden;
}

.certificates{
  &__top{
    @include grid100gap(2.5em);
    max-width: 52em;
    .text18{
      letter-spacing: 0.02em;
    }
  }
}

.certificates-slider{
  margin-top: 1.875em;
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.certificate-card{
  &__img{
    display: block;
    @include imgCover(141.3%);
    border-radius: .625em;
    box-shadow: -4px 5px 13px 0 rgba(0, 0, 0, 0.05), -14px 18px 23px 0 rgba(0, 0, 0, 0.04), -32px 41px 31px 0 rgba(0, 0, 0, 0.03), -58px 74px 38px 0 rgba(0, 0, 0, 0.01), -91px 114px 41px 0 rgba(0, 0, 0, 0);
  }
}

.feedback{
  padding-bottom: 5em;
  &__top{
    margin-top: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__desc{
    max-width: 27em;
    .text18{
      letter-spacing: 0.02em;
    }
  }
}

.feedback-contacts{
  &__title{
    max-width: 11.875em;
    .text18{
      letter-spacing: 0.02em;
    }
  }
  &__wrap{
    margin-left: 1.25em;
  }
  .socials{
    margin-right: 2.5em;
  }
}

.feedback-form{
  padding: 4.625em 5em 5em;
  border-radius: 1.25em;
  background-color: $gray3;
  margin-top: 2.5em;
  @include grid100gap(2.5em);
  position: relative;
  &__top{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5.625em / 3));
    gap: 1.875em;
  }
  &__bottom{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
  &__text{
    @media screen and (min-width: 993px){
      grid-column: 2/3;
      grid-row: 1/5;
      textarea{
        height: 100%;
      }
    }
  }
  &__services{
    grid-column: 1/-1;
    &-title{
      font-size: 1.25em;
      line-height: 115%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      font-weight: 700;
    }
    &-grid{
      margin-top: 1.25em;
      display: grid;
      grid-template-columns: repeat(4,calc(25% - 6em / 4));
      gap: .875em 2em;
    }
  }
  &._success{
    .m-success{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.m-success{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: inherit;
  border-radius: inherit;
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  background-image: url(../img/success-bg.svg);
  background-size: cover;
  background-position: center;
  padding: 4.125em 12.25em;
  &__img{
    margin-bottom: -2em;
    width: 11.875em;
  }
  &__title{
    font-size: 6.25em;
    line-height: 115%;
    font-weight: 700;
    color: $primary;
    letter-spacing: 0.02em;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .67em;
  }
}


@media screen and (max-width: 992px){
  .certificates{
    &__top{
      .text18{
        display: none;
      }
    }
  }

  .certificates-slider{
    margin-top: 2em;
    .swiper{
      &-container{
        width: calc(100% + .625em);
      }
      &-slide{
        padding-right: .625em;
        width: 17.0625em;
      }
    }
  }

  .feedback{
    padding-bottom: 3.75em;
    &__top{
      display: block;
      margin-top: 1.25em;
    }
  }

  .feedback-contacts{
    margin-top: 1.875em;
  }

  .feedback-form{
    margin-top: 1.875em;
    padding: 2.5em 1.25em;
    &__top,
    &__bottom{
      grid-template-columns: 100%;
    }
    &__services{
      &-title{
        font-size: 1.125em;
      }
      &-grid{
        grid-template-columns: repeat(2,calc(50% - 1em));
      }
    }
  }

  .m-success{
    padding: 2.5em;
    justify-content: center;
    &__img{
      width: 5em;
      margin-bottom: 0;
    }
    &__title{
      font-size: 3em;
    }
  }
}


@media screen and (max-width: 576px){
  .certificates{
    .h2{
      font-size: 1.75em;
    }
  }

  .feedback-contacts{
    display: block;
    &__title{
      max-width: 100%;
    }
    &__wrap{
      margin: .875em 0 0;
    }
  }

  .feedback-form{
    &__services{
      &-grid{
        grid-template-columns: 100%;
      }
    }
  }
}
