@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
.socials{
  display: flex;
  align-items: center;
  padding: 0 1.25em;
  position: relative;
  &:before,
  &:after{
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1px;
    height: 1.125em;
    border-radius: .4375em;
    background-color: $gray2;
  }
  &:before{
    left: 0;
  }
  &:after{
    right: 0;
  }
  &__item{
    width: 1.75em;
    margin-right: .625em;
    &:last-child{
      margin-right: 0;
    }
    img{
      width: 100%;
      display: block;
    }
    &:hover{
      transform: translateY(-10%);
    }
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.animated-opacity{
  &.animated{
    opacity: 0;
    transform: translateY(30px);
    transition: all 1s;
    &--active{
      transform: translateY(0);
      opacity: 1;
    }
  }
}


@media screen and (max-width: 992px){
  .socials{
    padding: 0;
    &:before,
    &:after{
      display: none;
    }
  }
}


