.main{
  padding: 11.875em 0 18.5em;
  background-image: url(../img/main-bg.svg);
  background-size: cover;
  background-position: center;
  border-radius: 0 0 1.25em 1.25em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 40.75em) 37.75em;
    gap: 1.25em 3em;
  }
  &__col{
    position: relative;
  }
  &__free{
    position: absolute;
    right: 8.125em;
    top: 5.5em;
    width: 10.5em;
  }
  &__desc{
    margin-top: 1.875em;
    max-width: 33.25em;
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
  }
  &__btn{
    margin-right: 1.25em;
    &:last-child{
      margin: 0;
    }
    svg{
      width: 1.375em;
      margin-left: .625em;
    }
  }
  .m-btn-blue-outline{
    &:hover{
      path{
        stroke: $white;
      }
    }
  }
  &__props{
    display: flex;
    margin-bottom: 3em;
  }
  &__prop{
    margin-right: 4.75em;
    &:last-child{
      margin: 0;
    }
    &-title{
      font-size: 1.875em;
      line-height: 117%;
      color: $primary;
      font-weight: 700;
      letter-spacing: 0.02em;
    }
    &-desc{
      font-size: 1.125em;
      line-height: 117%;
    }
  }
}

.main-slider{
  max-width: 26em;
  //margin: 0 auto;
  .swiper-slide{
    overflow: unset;
    //.main-slider__img{
    //  transform: scale(1.15) translateY(.0625em) translateX(-2.5em) rotateZ(4deg);
    //}
    //&-prev{
    //  .main-slider__img{
    //    transform: scale(1.085) translateY(.25em) translateX(-1em) rotateZ(2deg);
    //  }
    //}
    &-active{
      .main-slider__img{
        transform: unset;
      }
    }
    &-next{
      opacity: 1 !important;
      .main-slider__img{
        transform: translateX(5em);
      }
    }
    &-next+.swiper-slide{
      opacity: 1 !important;
      .main-slider__img{
        transform: translateX(10em);
      }
    }
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: inherit;
    img{
      border-radius: 1.625em;
      box-shadow: -4px 5px 13px 0 rgba(0, 0, 0, 0.05), -14px 18px 23px 0 rgba(0, 0, 0, 0.04), -32px 41px 31px 0 rgba(0, 0, 0, 0.03), -58px 74px 38px 0 rgba(0, 0, 0, 0.01), -91px 114px 41px 0 rgba(0, 0, 0, 0);
      width: 100%;
      display: block;
    }
  }
  .swiper-3d .swiper-slide-shadow{
    opacity: 0 !important;
    pointer-events: none;
  }
}

.easy-desc{
  padding-bottom: 3.75em;
  position: relative;
  z-index: 2;
  &__inner{
    padding: 5em;
    background-color: $black;
    border-radius: .625em;
    color: $white;
    margin-top: -11.8125em;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
  &__item{
    display: flex;
    align-items: center;
    &:nth-child(2){
      transition-delay: .2s;
    }
    &:nth-child(3){
      transition-delay: .4s;
    }
    &:nth-child(4){
      transition-delay: .6s;
    }
    &-icon{
      width: 4.375em;
      margin-right: .625em;
    }
    &-title{
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__text{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .main{
    overflow: hidden;
    padding: 6.25em 0 20em;
    &__inner{
      grid-template-columns: 100%;
    }
    .h1{
      br{
        display: none;
      }
    }
    &__props{
      margin-bottom: 1.25em;
    }
    &__prop{
      &-title{
        font-size: 1.625em;
      }
      &-desc{
        font-size: .875em;
      }
    }
    &__free{
      display: none;
    }
    &__desc{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .main-slider{
    max-width: 19em;
  }

  .easy-desc{
    padding-bottom: 1.875em;
    &__inner{
      margin-top: -16.4375em;
      padding: 2.5em 1.25em;
    }
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
    }
    &__item{
      &-icon{
        width: 3.125em;
      }
      &-title{
        font-size: .875em;
      }
    }
    &__text{
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    background-image: url(../img/main-bg2.svg);
    &__nav{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 0 0 .625em;
    }
    &__props{
      justify-content: space-between;
    }
    &__prop{
      margin: 0;
    }
  }

  .main-slider{
    .swiper{
      &-slide{
        &-next{
          .main-slider__img{
            transform: translateX(3em);
          }
        }
        &-next+.swiper-slide{
          .main-slider__img{
            transform: translateX(6em);
          }
        }
      }
    }
  }

  .easy-desc{
    .h2{
      font-size: 1.75em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
