.case-item,
.service-main{
  .breadcrumbs {
    li{
      &:not(:first-child) {
        &:before {
          background-image: url(../img/bread-circle2.svg);
        }
      }
    }
  }
}

.case-item{
  background-size: cover;
  background-position: center;
  padding: 9em 0 5em;
  color: $white;
  border-radius: 0 0 1.25em 1.25em;
  &:after{
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%), rgba(0, 0, 0, 0.2);;
  }
  &__inner{
    min-height: 34.125em;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 2;
  }
  .h1{
    margin-top: auto;
  }
}

.case-content{
  padding: 5em 0;
}

.case-item2{
  padding: 9em 0 5em;
  background-image: url(../img/case-item3.svg);
  background-size: cover;
  border-radius: 0 0 1.25em 1.25em;
}


@media screen and (max-width: 992px){
  .case-item{
    padding: 6.25em 0 3.75em;
    &__inner{
      min-height: 40em;
    }
  }

  .case-content{
    padding: 3.75em 0;
  }

  .case-item2{
    padding: 6.25em 0 2.5em;
  }
}


