.how-work{
  &__inner{
    padding: 5em;
    border-radius: 1.25em;
    background-color: $black;
    color: $white;
  }
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1.5em));
    gap: 1.875em;
  }
}

.step-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  &:nth-child(2){
    transition-delay: .2s;
  }
  &:nth-child(3){
    transition-delay: .4s;
  }
  &:nth-child(4){
    transition-delay: .6s;
  }
  &:nth-child(5){
    transition-delay: .8s;
  }
  &:last-child{
    &:before{
      display: none;
    }
  }
  &:before{
    content: '';
    position: absolute;
    background-image: url(../img/arrow.svg);
    width: 9.625em;
    height: 1.5em;
    left: 50%;
    margin-left: 3.5em;
    top: 1.125em;
    @include bgImgSize(100%);
  }
  &__num{
    width: 3.75em;
    height: 3.75em;
    @include center;
    background-color: $primary;
    border-radius: .625em;
    margin-bottom: 1.25em;
  }
  &__desc{
    letter-spacing: unset;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      padding-right: 1.875em;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.5em;
      display: flex;
      align-items: center;
      .swiper-button{
        position: static;
        margin: 0 0 0 1.875em;
      }
    }
  }
}

.swiper-button{
  width: 2.75em;
  height: 2.75em;
  border-radius: 50%;
  @include center;
  img{
    width: 1.5em;
  }
}

.clients-slider{
  margin-top: 2.5em;
  .swiper{
    &-slide{
      width: 20%;
    }
  }
}

.client-card{
  @include center;
  height: 6em;
  background-color: $gray3;
  border-radius: .625em;
  padding: 0 1.5em;
  &__img{
    max-height: 4em;
    max-width: 100%;
  }
}


@media screen and (max-width: 992px){
  .how-work{
    &__inner{
      padding: 2.5em .8125em;
    }
    &__items{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .step-card{
    text-align: left;
    flex-direction: row;
    &:before{
      background-image: url(../img/arrow2.svg);
      width: 1em;
      height: 2em;
      left: .75em;
      top: 100%;
      margin: .25em 0 0;
    }
    &__num{
      width: 2.5em;
      height: 2.5em;
      margin: 0 1.25em 0 0;
      flex-shrink: 0;
      .h2{
        font-size: 1.625em;
      }
    }
  }

  .slider{
    &__wrap{
      overflow: unset;
    }
    .swiper {
      &-container {
        width: calc(100% + 1.25em);
      }
      &-slide {
        padding-right: 1.25em;
      }
      &-nav{
        margin-bottom: 1.5em;
        .swiper-button{
          margin-left: 1.25em;
        }
      }
    }
  }

  .clients{
    overflow: hidden;
  }

  .clients-slider{
    margin-top: 1.5em;
    .swiper{
      &-slide{
        width: 17.6875em;
      }
    }
  }

  .client-card{
    &__img{
      flex-grow: 1;
    }
  }
}


@media screen and (max-width: 400px){
  .client-card{
    &__img{
      flex-grow: unset;
    }
  }
}
