.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0;
    border: none;
    border-bottom: 1px solid $black2;
    padding: 0;
    height: 2.2em;
    width: 100%;
    font-size: 1.25em;
    background-color: transparent;
    color: #000;
    letter-spacing: 0.02em;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding: 1.5em;
    height: 10em;
    resize: none;
    border-radius: .5em;
    background-color: $white;
    border: none;
  }
  &__field{
    position: relative;
  }
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.125em;
    min-height: 1.5em;
    span{
      font-size: 1em;
      line-height: 120%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid $gray;
      background-color: $white;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
      color: $blue;
    }
  }
  &._small{
    label{
      span{
        font-size: .875em;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $green;
        border-color: $green;
        background-size: 1.125em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .3125em;
      background-image: url(../img/check.svg);
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input,
    textarea{
      font-size: 1em;
      height: 2.5em;
    }
    textarea{
      padding: .875em;
      height: 9.375em;
    }
  }
}


