.cases{
  background-image: url(../img/cases-bg.svg);
  background-size: cover;
  border-radius: 1.25em;
  &__top{
    @include grid100gap(2.5em);
    max-width: 38em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
}

.cases-slider{
  margin-top: 1.875em;
  .swiper{
    &-slide{
      width: 50%;
    }
  }
}

.case-card{
  position: relative;
  display: block;
  border-radius: .625em;
  &__img{
    border-radius: .625em;
    @include imgCover(71%);
    &:after{
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 2.5em;
    color: $white;
    z-index: 2;
  }
}


@media screen and (max-width: 992px){
  .cases{
    overflow: hidden;
    &__top{
      gap: 1.25em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .cases-slider{
    margin-top: 1.25em;
  }

  .case-card{
    &__content{
      padding: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .cases{
    background-image: url(../img/cases-bg2.svg);
    &__inner{
      position: relative;
    }
    .h2{
      max-width: 5em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .cases-slider{
    position: static;
    .swiper{
      &-slide{
        width: 100%;
      }
      &-nav{
        bottom: unset;
        margin: 0;
        top: .8125em;
      }
    }
  }
}
