.service-main{
  background-image: url(../img/service-bg.jpg);
  background-size: cover;
  background-position: right center;
  padding: 9em 0 17.25em;
  color: $white;
  border-radius: 0 0 1.25em 1.25em;
  &__desc{
    margin-top: 1.875em;
    max-width: 46.25em;
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
  &__btn{
    margin-right: 1.25em;
    &:last-child{
      margin: 0;
    }
    svg{
      width: 1.375em;
      margin-left: .625em;
    }
  }
  &__badges{
    display: flex;
    margin-top: 1.875em;
  }
  &__badge{
    display: flex;
    align-items: center;
    height: 3.75em;
    padding: 0 2em;
    border-radius: .625em;
    background: rgba($white, 0.3);
    margin-right: .625em;
    &:last-child{
      margin: 0;
    }
    img{
      width: 1.375em;
      margin-right: .625em;
    }
  }
}

.prices{
  .easy-desc__inner{
    margin-top: -9.75em;
  }
}

.params{
  &__top{
    max-width: 52em;
    @include grid100gap(2.5em);
    .text18{
      letter-spacing: 0.02em;
    }
  }
}

.params-table{
  padding: 5em;
  border-radius: 1.25em;
  background-color: $gray3;
  margin-top: 2.5em;
  table{
    width: 100%;
  }
  td,
  th{
    padding: 1.25em 0;
    border-bottom: 1px solid $gray2;
    text-align: left;
    width: 50%;
  }
  th{
    padding-top: 0;
    .h4{
      letter-spacing: 0.02em;
      color: $primary;
    }
  }
}

.param-row{
  &__title{
    display: none;
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .service-main{
    padding: 6.25em 0 12.5em;
    background-image: unset;
    background-color: #1d5fac;
    &__desc{
      margin-top: 1.25em;
    }
    &__badges{
      margin-top: 1.25em;
    }
    &__badge{
      height: 3.375em;
      .h4{
        font-size: 1em;
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .prices{
    .easy-desc__inner{
      margin-top: -3.875em;
    }
  }

  .params {
    &__top {
      gap: 1.25em;
    }
  }

  .params-table{
    margin-top: 1.25em;
    padding: .625em;
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(.625em);
    }
    tr{
      @include grid100gap(1.25em);
    }
    td{
      @include grid100gap(.5em);
      border: none;
      padding: 0;
      width: 100%;
    }
  }

  .param-row{
    padding: 1.25em;
    border-radius: 1em;
    border: 1px solid $gray;
    &__title{
      letter-spacing: 0.02em;
      font-weight: 700;
      color: $primary;
      display: block;
    }
    &__desc{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .service-main{
    background-image: url(../img/service-bg2.jpg);
    background-position: center bottom;
    &__nav,
    &__badges{
      display: block;
    }
    &__btn,
    &__badge{
      width: 100%;
      margin: 0 0 .625em;
    }
  }

  ._block-mob{
    //display: inline-block;
  }
}
