.m-section{
  padding: 3.75em 0;
}

.m-section2{
  margin: 3.75em 0;
  padding: 7.5em 0;
}

.services{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .625em;
    grid-auto-flow: dense;
    margin-top: 1.875em;
  }
}

.service-card{
  display: block;
  border-radius: 1.25em;
  position: relative;
  &:hover{
    color: $black;
    transform: scale(1.01);
  }
  &._white{
    color: $white;
    &:hover{
      color: $white;
    }
  }
  @media screen and (min-width: 993px){
    &._big{
      grid-column: span 2;
      .service-card__content{
        max-width: 30em;
      }
    }
    &:nth-child(2){
      .service-card__desc{
        max-width: 17em;
      }
    }
    &:nth-child(7){
      .service-card__desc{
        max-width: 13.5em;
      }
    }
  }
  &__img{
    @include imgCover(100%);
    height: 15.625em;
    padding: 0;
    border-radius: 1.25em;
    ._view2{
      display: none;
    }
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 2.5em;
  }
  &__content{
    max-width: 18.75em;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__desc{
    margin-top: .875em;
    opacity: 0.9;
  }
  &__price{
    margin-top: auto;
    padding-top: 1em;
  }
}

.services-page{
  padding: 9em 0 10.75em;
  background-color: $gray3;
  &__desc{
    margin-top: 2.5em;
  }
}

.services-collection{
  padding: 0 0 5em;
  .cases__grid,
  .services__grid{
    margin-top: -5.75em;
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.875em 0;
  }

  .m-section2{
    margin: 1.875em 0;
    padding: 2.5em 0;
  }

  .services{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      margin-top: 1.25em;
    }
  }

  .service-card{
    &._big{
      grid-column: unset;
    }
    &__img{
      padding-bottom: 113.4%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__inner{
      padding: 1.25em;
    }
    &__desc{
      margin-top: 1.25em;
    }
    &__price{
      margin-top: 0;
    }
  }

  .services-page{
    padding: 6.25em 0 15.625em;
    border-radius: 0 0 1.25em 1.25em;
    &__desc{
      margin-top: 1.25em;
    }
  }

  .services-collection{
    padding: 0 0 3.75em;
    .cases__grid,
    .services__grid{
      margin-top: -11.875em;
    }
  }

}


@media screen and (max-width: 576px){
  .services{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
