.text{
  li,
  p{
    font-size: 1.125em;
    line-height: 160%;
  }
  ol,
  ul,
  p{
    @include lastM(0 0 1.25em);
  }
  hr{
    display: block;
    height: 3.5em;
    margin: 0;
    opacity: 0;
  }
  h1,
  h2,
  h3,
  h4{
    margin-bottom: 1em;
  }
  ol,ul{
    @include grid100gap(.125em);
    padding-left: .625em;
    li{
      &:before{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  ul{
    li{
      position: relative;
      padding-left: .9375em;
      &:before{
        content: 'â€¢';
      }
    }
  }
  ol{
    counter-reset: list;
    padding-left: .5em;
    li{
      counter-increment: list;
      position: relative;
      padding-left: 1em;
      &:before{
        content: counter(list) '. ';
      }
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    p{
      font-size: 1em;
    }
    h1,
    h2,
    h3,
    h4{
      margin-bottom: .67em;
    }
    hr{
      height: 2.5em;
    }
  }
}


