.footer{
  padding: 2.5em 0 2.125em;
  background-color: $black;
  color: $white;
  border-radius: 1.25em 1.25em 0 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 13.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__conf,
  &__c{
    .text18{
      letter-spacing: 0.02em;
    }
    span{
      color: $primary;
    }
  }
  &__by{
    width: 4.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0;
    &__inner{
      @include grid100gap(1.25em);
    }
  }
}


