@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.375em;
    text-transform: uppercase;
    border-radius: .625em;
    &:focus {
        box-shadow: none;
    }
    span{
        letter-spacing: 0.02em;
        font-size: 1.125em;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: #32a4ff;
        color: $white;
    }
}

.m-btn-white{
    background-color: $white;
    color: $blue;
    &:hover{
        background-color: #32a4ff;
        color: $white;
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}
