.breadcrumbs {
    padding: 0 0 2.5em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        &:not(:first-child) {
            margin-left: 1.5em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .3125em;
                right: 100%;
                margin-right: .5em;
                width: .5em;
                height: .5em;
                background-image: url(../img/bread-circle.svg);
                @include bgImgSize(100%);
            }
        }
        &:last-child{
            pointer-events: none;
        }
    }
    a{
        font-size: 1.125em;
        line-height: 117%;
    }
}



@media screen and (max-width: 992px){
    .breadcrumbs{
        padding-bottom: 1.25em;
        white-space: nowrap;
        overflow-x: auto;
        width: calc(100% + 2.5em);
        margin-left: -1.25em;
        padding-left: 1.25em;
        padding-right: 1.25em;
        &::-webkit-scrollbar {
            width: 0;
            height: 0;
            display: none;
        }
    }
}
